/* ==========================================================================
   Header
   ========================================================================== */
.l-header {
  background-color: #fff;
  box-shadow: 0 2px 8px #f0f1f2;
  position: relative;
  z-index: 11; }

.l-headerContent {
  display: flex;
  justify-content: space-between; }
  .l-headerContent__logo {
    padding: 8px 40px; }
  .l-headerContent__burger {
    display: none;
    width: 24px;
    height: 24px; }
  @media only screen and (max-width: 1023px) {
    .l-headerContent__logo {
      padding: 8px 16px; }
    .l-headerContent__burger {
      display: block;
      position: fixed;
      z-index: 12;
      right: 16px;
      top: 13px; } }

.o-logo {
  height: 36px; }
  .o-logo img {
    max-width: 100%; }

/* ==========================================================================
   Main
   ========================================================================== */
html,
body {
  width: 100%;
  overflow-x: hidden; }

.l-wrap {
  max-width: 1300px;
  width: 100%;
  margin: 0 auto;
  padding: 0 16px;
  box-sizing: border-box; }

.l-mainWrapper {
  display: flex; }
  .l-mainWrapper__side {
    width: 16%;
    min-width: 250px;
    flex-shrink: 0;
    background-color: #fff; }
  .l-mainWrapper__main {
    width: 84%; }
  @media only screen and (max-width: 1023px) {
    .l-mainWrapper {
      width: 100%;
      display: block; }
      .l-mainWrapper__side {
        position: fixed;
        left: -300px;
        top: 52px;
        z-index: 10;
        width: 40%;
        height: 100%;
        max-width: 300px;
        border-right: 1px solid #e8e8e8;
        box-shadow: 0 2px 8px #f0f1f2;
        transition: left .3s; }
        .is-menuOpen .l-mainWrapper__side {
          left: 0; }
      .l-mainWrapper__main {
        width: 100%; } }

.l-main {
  padding: 0 170px 40px 40px;
  box-sizing: border-box; }
  @media only screen and (max-width: 1023px) {
    .l-main {
      width: 100%;
      padding: 40px 16px; } }

.c-menu {
  max-height: 100vh;
  overflow-x: hidden;
  overflow-y: hidden; }
  .c-menuL1 {
    margin: 0;
    padding: 0 0 48px 0;
    list-style: none;
    border-right: 1px solid #e8e8e8;
    box-sizing: border-box; }
    .c-menuL1__item {
      max-height: 40px;
      overflow: hidden;
      border-bottom: 1px solid #e8e8e8; }
      .c-menuL1__item.is-chosen {
        max-height: 1000px;
        transition: max-height .6s; }
    .c-menuL1__title {
      position: relative;
      padding-left: 40px;
      padding-right: 40px;
      height: 40px;
      line-height: 40px;
      cursor: pointer; }
      .c-menuL1__title h4 {
        margin: 0;
        padding: 0; }
      .c-menuL1__title .c-icon {
        content: '';
        position: absolute;
        right: 16px;
        top: 0; }
  .c-menuL2 {
    margin: 0;
    padding: 0;
    list-style: none; }
    .c-menuL2__item a {
      display: block;
      padding-left: 80px;
      padding-right: 16px;
      height: 40px;
      line-height: 40px;
      overflow: hidden;
      text-overflow: ellipsis;
      font-size: 14px; }
    .c-menuL2__item.is-current {
      position: relative;
      background-color: #fff7e6; }
      .c-menuL2__item.is-current:after {
        content: '';
        border-right: 3px solid #fa8c16;
        position: absolute;
        top: 0;
        right: -1px;
        bottom: 0; }
    .c-menuL2__item:hover a,
    .c-menuL2__item.is-current a {
      color: #ff8300; }

.c-headerContentBurger {
  fill: #ff8300; }
  .c-headerContentBurger:hover {
    fill: #e47601; }

/* ==========================================================================
   Markdown
   ========================================================================== */
.l-markdown {
  font-size: 14px;
  line-height: 2; }
  .l-markdown .L1 {
    margin-top: 8px;
    margin-bottom: 20px;
    font-size: 30px; }
  .l-markdown .L2 {
    font-size: 24px; }
  .l-markdown .L3 {
    font-size: 18px; }
  .l-markdown .L4 {
    font-size: 16px; }
  .l-markdown .L2,
  .l-markdown .L3,
  .l-markdown .L4,
  .l-markdown .L5,
  .l-markdown .L6 {
    clear: both;
    margin: 1.6em 0 .6em; }
  .l-markdown p,
  .l-markdown pre {
    margin: 1em 0; }
  .l-markdown blockquote {
    margin: 1em 0;
    padding-left: .8em;
    color: #8B8B8B;
    font-size: 90%;
    border-left: 4px solid #ebedf0; }
  .l-markdown hr {
    clear: both;
    height: 1px;
    margin: 56px 0;
    background: #ebedf0;
    border: 0; }
  .l-markdown > ul {
    margin: 0;
    padding: 0; }
    .l-markdown > ul > li {
      margin-left: 20px;
      padding-left: 4px;
      list-style-type: circle; }
  .l-markdown > ol {
    margin: 0;
    padding: 0; }
    .l-markdown > ol > li {
      margin-left: 20px;
      padding-left: 4px;
      list-style-type: decimal; }

/* ==========================================================================
   Anchor
   ========================================================================== */
.l-anchor {
  position: fixed;
  top: 92px;
  right: 16px; }
  @media only screen and (max-width: 767px) {
    .l-anchor {
      display: none; } }

.c-anchor {
  list-style-type: none;
  margin: 0;
  padding: 0; }
  .c-anchor li {
    margin-bottom: 4px; }
    .c-anchor li a {
      display: block;
      padding-left: 16px;
      width: 110px;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      border-left: 1px solid #e8e8e8;
      font-size: 12px; }
      .c-anchor li a:hover {
        color: #ff8300;
        border-left: 1px solid #ff8300; }

/* ==========================================================================
   Footer
   ========================================================================== */
.l-footer {
  padding: 40px;
  background-color: #000;
  color: #fff;
  text-align: center; }
