@import "./_variables";
@import "./_mixins";


/* ==========================================================================
   Header
   ========================================================================== */
.l-header {
    background-color: #fff;
    box-shadow: 0 2px 8px #f0f1f2;
    position: relative;
    z-index: 11;
}

.l-headerContent {
    display: flex;
    justify-content: space-between;

    &__logo {
        padding: 8px 40px;
    }

    &__burger {
        display: none;
        width: 24px;
        height: 24px;
    }

    @include breakpoint-down("tablet") {
        &__logo {
            padding: 8px 16px;
        }

        &__burger {
            display: block;
            position: fixed;
            z-index: 12;
            right: 16px;
            top: 13px;
        }
    }

}

.o-logo {
    height: 36px;

    img {
        max-width: 100%;
    }
}

/* ==========================================================================
   Main
   ========================================================================== */
html,
body {
    width: 100%;
    overflow-x: hidden;
}

.l-wrap {
    max-width: 1300px;
    width: 100%;
    margin: 0 auto;
    padding: 0 16px;
    box-sizing: border-box;
}

.l-mainWrapper {
    display: flex;

    &__side {
        width: 16%;
        min-width: 250px;
        flex-shrink: 0;
        background-color: #fff;
    }

    &__main {
        width: 84%;
    }

    @include breakpoint-down("tablet") {
        width: 100%;
        display: block;

        &__side {
            position: fixed;
            left: -300px;
            top: 52px;
            z-index: 10;
            width: 40%;
            height: 100%;
            max-width: 300px;
            border-right: 1px solid $color-border-main;
            box-shadow: 0 2px 8px #f0f1f2;
            transition: left .3s;

            .is-menuOpen & {
                left: 0;
            }
        }

        &__main {
            width: 100%;
        }
    }
}

.l-main {
    padding: 0 170px 40px 40px;
    box-sizing: border-box;

    @include breakpoint-down("tablet") {
        width: 100%;
        padding: 40px 16px;
    }
}

.c-menu {
    max-height: 100vh;
    overflow-x: hidden;
    overflow-y: hidden;

    &L1 {
        margin: 0;
        padding: 0 0 48px 0;
        list-style: none;
        border-right: 1px solid $color-border-main;
        box-sizing: border-box;

        &__item {
            max-height: 40px;
            overflow: hidden;
            border-bottom: 1px solid $color-border-main;

            &.is-chosen {
                max-height: 1000px;
                transition: max-height .6s;
            }
        }

        &__title {
            position: relative;
            padding-left: 40px;
            padding-right: 40px;
            height: 40px;
            line-height: 40px;
            cursor: pointer;

            h4 {
                margin: 0;
                padding: 0;
            }

            .c-icon {
                content: '';
                position: absolute;
                right: 16px;
                top: 0;
            }
        }
    }

    &L2 {
        margin: 0;
        padding: 0;
        list-style: none;

        &__item {
            a {
                display: block;
                padding-left: 80px;
                padding-right: 16px;
                height: 40px;
                line-height: 40px;
                overflow: hidden;
                text-overflow: ellipsis;
                font-size: 14px;
            }

            &.is-current {
                position: relative;
                background-color: $color-bg-opacity;

                &:after {
                    content: '';
                    border-right: 3px solid #fa8c16;
                    position: absolute;
                    top: 0;
                    right: -1px;
                    bottom: 0;
                }

            }

            &:hover a,
            &.is-current a {
                color: $color-font-highlight;
            }

        }
    }
}

.c-headerContentBurger {
    fill: $color-bg-highlight;

    &:hover {
        fill: $color-bg-darker;
    }
}

/* ==========================================================================
   Markdown
   ========================================================================== */
.l-markdown {
    font-size: 14px;
    line-height: 2;

    .L1 {
        margin-top: 8px;
        margin-bottom: 20px;
        font-size: 30px;
    }

    .L2 {
        font-size: 24px;
    }

    .L3 {
        font-size: 18px;
    }

    .L4 {
        font-size: 16px;
    }


    .L2,
    .L3,
    .L4,
    .L5,
    .L6 {
        clear: both;
        margin: 1.6em 0 .6em;
    }

    p,
    pre {
        margin: 1em 0;
    }

    blockquote {
        margin: 1em 0;
        padding-left: .8em;
        color: $color-font-gray;
        font-size: 90%;
        border-left: 4px solid $color-border-light;
    }

    hr {
        clear: both;
        height: 1px;
        margin: 56px 0;
        background: $color-bg-light;
        border: 0;
    }

    &>ul {
        margin: 0;
        padding: 0;

        &>li {
            margin-left: 20px;
            padding-left: 4px;
            list-style-type: circle;
        }
    }

    &>ol {
        margin: 0;
        padding: 0;

        &>li {
            margin-left: 20px;
            padding-left: 4px;
            list-style-type: decimal;
        }
    }
}

/* ==========================================================================
   Anchor
   ========================================================================== */

.l-anchor {
    position: fixed;
    top: 92px;
    right: 16px;

    @include breakpoint-down("mobile") {
        display: none;
    }
}

.c-anchor {
    list-style-type: none;
    margin: 0;
    padding: 0;

    li {
        margin-bottom: 4px;

        a {
            display: block;
            padding-left: 16px;
            width: 110px;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            border-left: 1px solid $color-border-main;
            font-size: 12px;

            &:hover {
                color: $color-font-highlight;
                border-left: 1px solid $color-border-highlight;
            }
        }
    }
}

/* ==========================================================================
   Footer
   ========================================================================== */
.l-footer {
    padding: 40px;
    background-color: #000;
    color: #fff;
    text-align: center;
}